export default {
  addEventListener (element, event, handler) {
    if (typeof window === 'undefined' || !element || !event) return
    if (document.removeEventListener) {
      element.addEventListener(event, handler, false)
    } else {
      element.attachEvent(`on${event}`, handler)
    }
  },

  removeEventListener (element, event, handler) {
    if (typeof window === 'undefined' || !element || !event) return

    if (document.removeEventListener) {
      element.removeEventListener(event, handler, false)
    } else {
      element.detachEvent(`on${event}`, handler)
    }
  },

  setClass (el, cls) {
    if (typeof cls !== 'string' || !cls) return
    if (el.nodeType !== 1 && !el.className) return
    let clses = (cls || '').match(/\S+/g) || []
    let cur = ''
    for (let cls of clses) {
      cur += `${cls} `
    }
    el.className = cur.trim()
  },

  addClass (el, cls) {
    if (!(typeof cls === 'string' && cls)) return
    let cur = el.nodeType === 1 && (el.className          // htmlclass
      ? (` ${el.className} `).replace(/[\t\r\n\f]/g, ' ')
      : ' '
    )
    if (!cur) return

    let clses = (cls || '').match(/\S+/g) || []

    for (let cls of clses) {
      if (cur.includes(` ${cls} `)) continue              // htmlclassclass
      cur += `${cls} `
    }
    el.className = cur.trim()                             // 
  },

  removeClass (el, cls) {
    if (!(typeof cls === 'string' && cls)) return
    let cur = el.nodeType === 1 && (el.className
      ? (` ${el.className} `).replace(/[\t\r\n\f]/g, ' ')
      : ' '
    )
    if (!cur) return

    let clses = (cls || '').match(/\S+/g) || []

    for (let cls of clses) {
      if (!cur.includes(` ${cls} `)) continue
      cur = cur.replace(` ${cls} `, ' ')
    }
    el.className = cls ? cur.trim() : ''                  // class
  }
}
