export default {
  methods: {
   /*
    * @Desc: 
    * @Parm: parent -> name
             e      -> 
             value  -> 
    */
    dispatch (parentName, e, value) {
      let parent = this.$parent || this.$root   // $root, Vue
      let name = parent.$options.name

      while (parent && (!name || name !== parentName)) {
        parent = parent.$parent
        if (parent) {
          name = parent.$options.name
        }
      }

      if (parent) {
        parent.$emit(e, value)
      }
    }
  }
}
