
export default {
  getWindowsScrollOffsets (w) {
    w = w || window                               // windows

    if (w.pageXOffset != null) {                  // IE8
      return {
        x: w.pageXOffset,
        y: w.pageYOffset
      }
    }

    let d = w.document
    if (document.compatMode === 'CSS1Compat') {    // IE
      return {
        x: d.documentElement.scrollLeft,
        y: d.documentElement.scrollTop
      }
    }

    return {                                        // 
      x: d.body.scrollLeft,
      y: d.body.scrollTop
    }
  },

  getViewPortSize (w) {
    w = w || window

    if (w.innerWidth) {
      return {
        x: w.innerWidth,
        y: w.innerHeight
      }
    }

    let d = document
    if (document.compatMode === 'CSS1Compat') {
      return {
        x: d.documentElement.clientWidth,
        y: d.documentElement.clientHeight
      }
    }

    return {
      x: d.body.clientWidth,
      y: d.body.clientHeight
    }
  }

}

